import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DeletedCircleComponent } from './deletedCircle.component';

const routes: Routes = [
    {
        path: '',
        component: DeletedCircleComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DeletedCircleRoutingModule {}
